.container {
  height: 100%;
  padding-inline: 30px;
}

@media (width >= 640px) {
  .container {
    margin-inline: auto;
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .container {
    padding-inline: 123px;
  }
}

h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Inter;
  src: url("Inter-VariableFont_opsz,wght.25edf01c.ttf") format("truetype");
  font-weight: 100 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.button__style {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #333;
  font-family: Inter, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 100%;
}

@media (width >= 640px) {
  .button__style {
    width: 100px;
    height: 48px;
  }
}

.header__title {
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 20px;
  font-family: Inter, sans-serif;
  font-size: 40px;
  font-weight: 700;
  line-height: 52px;
}

@media (width >= 640px) {
  .header__title {
    grid-column: 1 / 5;
    margin-bottom: 30px;
    font-size: 48px;
    line-height: 62px;
  }
}

@media (width >= 1280px) {
  .header__title {
    font-size: 62px;
    line-height: 78px;
  }
}

.header__text {
  margin: 0;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

@media (width >= 640px) {
  .header__text {
    grid-column: 1 / 4;
  }
}

.header__menu {
  display: none;
}

@media (width >= 640px) {
  .header__menu {
    align-items: center;
    gap: 30px;
    display: flex;
  }

  .header__menu--text {
    color: #4f4f4f;
    font-family: Inter, sans-serif;
    font-size: 14px;
    font-weight: 300;
    line-height: 100%;
  }
}

.header__menu--top {
  align-items: center;
  display: flex;
}

.header__photo {
  margin-bottom: 40px;
}

@media (width >= 640px) {
  .header__photo {
    position: absolute;
    right: 0;
  }

  .header__contant {
    --colums: 2;
    grid-template-columns: repeat(var(--colums), 1fr);
    column-gap: 20px;
    display: grid;
    position: relative;
  }
}

@media (width >= 640px) and (width >= 640px) {
  .header__contant {
    --colums: 8;
    column-gap: 20px;
  }
}

@media (width >= 640px) and (width >= 1280px) {
  .header__contant {
    --colums: 12;
    column-gap: 30px;
  }
}

.icon {
  width: 32px;
  height: 32px;
  display: block;
}

.icon--menu {
  background-image: url("Icon-Burger-menu.eeafa77f.png");
  margin-left: 30px;
}

@media (width >= 1280px) {
  .icon--menu {
    display: none;
  }
}

.page {
  scroll-behavior: smooth;
  margin: 0;
}

.page__body {
  min-width: 320px;
  margin: 0;
}

.top-bar {
  padding-top: 25px;
  padding-bottom: 25px;
  justify-content: space-between;
  align-items: center;
  max-width: 1440px;
  padding-inline: 30px;
  display: flex;
}

@media (width >= 640px) {
  .top-bar {
    padding-top: 40px;
    padding-bottom: 40px;
    margin-bottom: 100px;
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .top-bar {
    margin-inline: auto;
    margin-bottom: 60px;
    padding-inline: 123px;
  }
}

.top-bar__logo-link {
  display: flex;
}

.top-bar__logo {
  height: 26px;
}

body {
  background: #f7f7f7;
}

* {
  box-sizing: border-box;
}
